<script>
import dayjs from "dayjs";

export default {
  // 格式化日期过滤器
  filters: {
    date(val) {
      return dayjs(val).format("MM/DD");
    },
  },
  data() {
    return {
      //轮播图js功能
      banners: ["/1.jpg", "/2.jpg", "/3.jpg"],
      swiperOption: {
        autoplay: true,
        pagination: {
          el: ".swiper-pagination",
        },
      },
      //精灵图
      sprite: {
        news: "爆料站",
        Story: "故事站",
