      },

      //新闻资讯数据
      newsCats: [],
      //英雄列表数据
      heroCats: [],
    };
  },

  methods: {
    async fetchNewsCats() {
      const res = await this.$http.get("news/list");
      this.newsCats = res.data;
    },
    async fetchHeroCats() {
      const res = await this.$http.get("hero/list");
      console.log(res);
      this.heroCats = res.data;
    },
  },
  created() {
    this.fetchNewsCats();
    this.fetchHeroCats();
  },
};
