<template>
  <div class="page-article" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom" @click="$router.push('/')">
      <div class="iconfont icon-Back text-blue"></div>
      <strong class="flex-1 text-blue pl-2">
        {{ model.title }}
      </strong>
      <div class="text-grey fs-xs">2021-06-19</div>
    </div>

    <div v-html="model.body" class="px-3 article-body fs-lg"></div>
  </div>
</template>

<script>
export default {
  //接收路由变量
  props: {
    id: { required: true },
  },

  data() {
    return {
      model: null,
    };
  },

  methods: {
    // 获取新闻文章数据
    async fetch() {
      const res = await this.$http.get(`articles/${this.id}`);
      console.log(213);
      this.model = res.data;
    },
  },

  created() {
    this.fetch();
  },
};
</script>


